import React, { useState, useRef } from 'react';
// import IconButton from '@mui/material/IconButton';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import { Button } from '@mui/material';
import GetAppIcon from '@mui/icons-material/GetApp';
import PublishIcon from '@mui/icons-material/Publish';
// import CloudUploadIcon from '@mui/icons-material/CloudUpload';
// import CloudDownloadIcon from '@mui/icons-material/CloudDownload';

const DataJson = () => {
    // eslint-disable-next-line
    const [fileData, setFileData] = useState(null);
    const fileInputRef = useRef(null);

    const handleExport = async () => {
        try {
            // Fetch data from the service
            const response = await fetch('/service/get-json');
            const data = await response.json();
    
            // Extract config_data from the response
            const { config_data } = data;
    
            // Prompt user for a new file name
            const newFileName = prompt('Enter a new file name:', 'DatabaseConfig');
            if (!newFileName) {
                // User canceled or entered an empty name
                return;
            }
    
            // Construct the new file name
            const fileName = newFileName.endsWith('.json') ? newFileName : `${newFileName}.json`;
    
            // Stringify the data with proper formatting
            const fileContent = JSON.stringify(config_data, null, 2);
    
            // Create a Blob with the JSON content
            const blob = new Blob([fileContent], { type: 'application/json' });
    
            // Create a download URL for the Blob
            const url = URL.createObjectURL(blob);
    
            // Trigger download using an anchor tag
            const link = document.createElement('a');
            link.href = url;
            link.download = fileName;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        } catch (error) {
            console.error('Error fetching or processing file:', error);
        }
    };
    

    const handleImport = async (event) => {
        try {
            const file = event.target.files[0];
            const formData = new FormData();
            formData.append('file', file);

            const response = await fetch('/service/upload-json', {
                method: 'POST',
                body: formData,
            });

            console.log('File uploaded successfully:', response);
        } catch (error) {
            console.error('Error uploading file:', error);
        }
    };

  return (
      <Container>
        <Box position="absolute" top={0} right={0} padding="10px" marginRight="270px" display="flex">

            {/* Import Button */}
            <input
              type="file"
              accept=".json"
              style={{ display: 'none' }}
              onChange={handleImport}
              ref={fileInputRef}
            />
            <Button
                variant="contained"
                startIcon={<GetAppIcon />}
                style={{ marginRight: '10px', backgroundColor: 'rgb(204, 85, 0)' }}
                onClick={() => fileInputRef.current.click()}
            >
                {/* <CloudDownloadIcon /> */}
                Import
            </Button>

            {/* Export Button */}
            <Button
                variant="contained"
                startIcon={<PublishIcon />}
                style={{ backgroundColor: 'rgb(204, 85, 0)' }}
                onClick={handleExport}
            >
                {/* <CloudUploadIcon /> */}
                Export
            </Button>
        </Box>
    </Container>
  );
};

export default DataJson;
